var server = window.location.hostname;
var port = "1337";
var Graph = /** @class */ (function () {
    function Graph(vector, id_graph) {
        this.vector = vector;
        this.id_graph = id_graph;
    }
    return Graph;
}());
var button = document.getElementById('search_click');
var id_graph = document.getElementById("id_graph");
var vector = document.getElementById("vector");
var status_request = document.getElementById("status_request");
function updateGraphInfo() {
    var inputValue = document.getElementById("graph").value;
    fetch("http://" + server + ":" + port + "/graph/?id=" + inputValue)
        .then(function (response) {
        return response.json().then(function (data) { return ({ status: response.status, body: data }); });
    }).then(function (data) {
        var graph = new Graph("", "");
        if (data.status == 200) {
            graph.vector = "[" + data.body.vector.join(",") + "]";
            graph.id_graph = data.body.id_graph;
            status_request.innerHTML = "";
            vector.innerHTML = graph.vector;
            id_graph.innerHTML = graph.id_graph;
        }
        else if (data.status == 404) {
            status_request.innerHTML = "Not Found";
            vector.innerHTML = "";
            id_graph.innerHTML = "";
        }
    });
}
button.addEventListener('click', updateGraphInfo);
